import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'

Vue.use(Router)


export default new Router ({   //  
    mode: 'history', //   
    routes: [      //  
        {
            path: '/',
            component: Home
        },
        {
            path: '/galleryPage',
            component: () => import ('./views/GalleryPage.vue')
        },
        {
            path: '/ourProducts',
            component: () => import ('./views/OurProducts.vue')
        }
    ]
})